import './AdminProducto.css'
import '@pathofdev/react-tag-input/build/index.css'

import { IonAlert, IonSpinner } from '@ionic/react'
import ReactTagInput from '@pathofdev/react-tag-input'
import { observer } from 'mobx-react-lite'
import React, { useRef, useState } from 'react'
import { RouteComponentProps } from 'react-router'

import {AdminItemField, LoaderAdmin, Page, VideoLoaderModal} from '../../components'
import SelectorModal from '../../components/SelectorModal'
import { RUTAS, TIPOS_REFRIGERACION_SELECT } from '../../constants'
import { cambiar_formato_numero } from '../../helpers'
import {DetailViewProps, useToaster} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { Producto } from '../../services/api'
import {Capacitor} from "@capacitor/core";
import {Filesystem} from "@capacitor/filesystem";

export const AdminProductoUpdate: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { adminStore, loginStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Ready, setReady] = useState(false)
  const [Errors, setErrors] = useState<Producto | null>(null)
  const [Producto, setProducto] = useState(null as any)
  const ProductoRef = useRef(null as any)
  const [tags, setTags] = useState<string[]>([])
  const Estado = match.params ? (match.params as any).estado : 'moderado'
  const ProductoId = match.params.id
  const [Alert, setAlert] = useState(false)
  const [TituloAlert, setTituloAlert] = useState('')
  const [TextoAlert, setTextoAlert] = useState('')
  const addToast = useToaster()
  const Editando = !!ProductoId

  const setup = () => {
    obtenerCategorias()
    //
    setProducto(null)
    ProductoRef.current = null
    if (ProductoId !== undefined) {
      obtenerProducto()
    } else {
      setReady(true)
    }
  }

  React.useEffect(() => {
    setup()
  }, [ProductoId])

  const obtenerProducto = () => {
    setLoading(true)
    rootStore.environment.api.getProductoAdmin(ProductoId).then((result) => {
      if (result.kind === 'ok') {
        setProducto(result.data)
        ProductoRef.current = result.data
        setTags(result.data.etiquetas.split(','))
        setReady(true)
      } else {
        addToast('Ocurrió un error al conectarse al servidor, vuelva a intentar en unos momentos', 'error')
      }
      setLoading(false)
    })
  }

  const obtenerCategorias = () => {
    rootStore.environment.api.categorias().then((result) => {
      if (result.kind === 'ok') {
        // @ts-ignore
        rootStore.setCategorias(result.categorias)
      }
    })
  }

  const guardar = () => {
    if (Producto) {
      setLoading(true)
      setErrors(null)
      let fabricante
      let categoria = null
      if (Producto.categoria !== undefined) {
        if (typeof Producto.categoria === 'number') {
          categoria = Producto.categoria
        } else {
          categoria = Producto.categoria.id
        }
      }

      if (Producto.fabricante !== undefined) {
        if (typeof Producto.fabricante === 'number') {
          fabricante = Producto.fabricante
        } else {
          if (!loginStore.administrador && loginStore.fabricante_id) {
            fabricante = loginStore.fabricante_id
          } else {
            fabricante = Producto.fabricante.id
          }
        }
      }

      const etiquetas = tags.join(',')
      let datos = { ...Producto, etiquetas: etiquetas, categoria: categoria, fabricante: fabricante }
      if (!loginStore.administrador && loginStore.fabricante_id) {
        datos = { ...datos, fabricante: loginStore.fabricante_id }
      }
      if (datos.peso_kg) {
        datos = { ...datos, peso_kg: cambiar_formato_numero(datos.peso_kg) }
      }
      if (datos.alto_cm) {
        datos = { ...datos, alto_cm: cambiar_formato_numero(datos.alto_cm) }
      }
      if (datos.ancho_cm) {
        datos = { ...datos, ancho_cm: cambiar_formato_numero(datos.ancho_cm) }
      }
      if (datos.profundidad_cm) {
        datos = { ...datos, profundidad_cm: cambiar_formato_numero(datos.profundidad_cm) }
      }
      if (!loginStore.administrador && Producto.id) {
        datos.tipo = 'detalle'
      }
      rootStore.environment.api.guardarProducto(datos).then((result: any) => {
        procesarResultado(result)
      })
    } else {
      addToast('Complete todos los campos para continuar', 'warning')
    }
  }

  const procesarResultado = (result: any) => {
    setLoading(false)
    if (result.kind === 'ok') {
      addToast('Producto guardado satisfactoriamente', 'success')
      if (Producto && Producto.id === undefined && adminStore.fabricanteDetalle) {
        addToast(
          'El producto se ha agregado con exito y está pendiente de aprobación por parte del staff de ABi',
          'warning',
        )
      }
      setProducto(null)
      ProductoRef.current = null
      history.push(RUTAS.ADMIN_PRODUCTO_DETAIL(Estado, result.response.id))
    } else if (result.kind === 'bad-data') {
      setErrors(result.errors)
      if (result.errors.non_field_errors) {
        addToast(result.errors.non_field_errors[0], 'warning')
      } else {
        addToast('Se han encontrado errores de validación', 'warning')
      }
    } else {
      addToast('Ocurrió un error al guardar vuelva a intentar en unos momentos', 'error')
    }
  }

  const setDatoInput = (campo: string, data: any) => {
    setDato(campo, data)
  }

  const setDato = (campo: string, valor: any) => {
    let datos = { ...ProductoRef.current }
    // @ts-ignore
    datos[campo] = valor
    setProducto(datos as any)
    ProductoRef.current = datos
  }

  const setDatos = (Datos: any) => {
    let datos = { ...ProductoRef.current, ...Datos }
    setProducto(datos as any)
    ProductoRef.current = datos
  }

  const dataSelectorModal = (data: any) => {
    setDato('fabricante', data[0])
  }

  const dataSelectorModalCategoria = (data: any) => {
    setDato('categoria', data[0])
  }

  const videoSelected = async (data: any) => {
    if (data === null) {
      setDatos({
        delete_video: true,
        video: null,
      })
      return
    }
    if (Capacitor.getPlatform() !== 'web') {
      const contents = await Filesystem.readFile({
        path: data,
      })
      data = `data:video/mp4;base64,${contents.data}`
    }
    let blob = await fetch(data).then((r) => r.blob())
    setDatos({
      delete_video: false,
      video: blob,
    })
  }

  return (
    <Page login_required admin>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TituloAlert}
        message={TextoAlert}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'btn-secondary',
          },
          {
            text: 'Confirmar',
            handler: () => guardar(),
          },
        ]}
      />
      <LoaderAdmin visible={Loading} />
      {Ready && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">{Editando ? 'Editar producto' : 'Crear producto'}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/producto/' + Estado)}>Productos</a>
                  </li>
                  <li className="breadcrumb-item active">{Editando ? 'Editar' : 'Crear'}</li>
                </ol>
              </div>
            </div>
            <div className="card mb-0">
              <div className="card-body">
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Nombre'}
                    tipo={'texto'}
                    campo={'nombre'}
                    valor={Producto ? Producto.nombre : ''}
                    error={Errors && Errors.nombre}
                    set_dato={setDatoInput}
                    columnas={4}
                  />
                  <AdminItemField
                    titulo={'Descripción Corta'}
                    tipo={'texto'}
                    campo={'descripcion'}
                    valor={Producto ? Producto.descripcion : ''}
                    error={Errors && Errors.descripcion}
                    set_dato={setDatoInput}
                    columnas={8}
                  />
                  <AdminItemField
                    titulo={'Descripción Extensa'}
                    tipo={'textarea'}
                    campo={'descripcion_extensa'}
                    valor={Producto ? Producto.descripcion_extensa : ''}
                    error={Errors && Errors.descripcion_extensa}
                    set_dato={setDatoInput}
                    columnas={12}
                  />
                  <AdminItemField
                    titulo={'Especificaciones'}
                    tipo={'textarea'}
                    campo={'especificaciones'}
                    valor={Producto ? Producto.especificaciones : ''}
                    error={Errors && Errors.especificaciones}
                    set_dato={setDatoInput}
                    columnas={12}
                  />
                </div>
                <div className={'row mt-4'}>
                  <AdminItemField
                    titulo={'Peso'}
                    tipo={'decimales'}
                    campo={'peso_kg'}
                    valor={Producto ? Producto.peso_kg : ''}
                    error={Errors && Errors.peso_kg}
                    set_dato={setDatoInput}
                    columnas={3}
                    help_text={'Peso del producto en kilogramos'}
                  />
                  <AdminItemField
                    titulo={'Altura'}
                    tipo={'decimales'}
                    campo={'alto_cm'}
                    valor={Producto ? Producto.alto_cm : ''}
                    error={Errors && Errors.alto_cm}
                    set_dato={setDatoInput}
                    columnas={3}
                    help_text={'Altura del producto en centímetros'}
                  />
                  <AdminItemField
                    titulo={'Ancho'}
                    tipo={'decimales'}
                    campo={'ancho_cm'}
                    valor={Producto ? Producto.ancho_cm : ''}
                    error={Errors && Errors.ancho_cm}
                    set_dato={setDatoInput}
                    columnas={3}
                    help_text={'Ancho del producto en centímetros'}
                  />
                  <AdminItemField
                    titulo={'Profundidad'}
                    tipo={'decimales'}
                    campo={'profundidad_cm'}
                    valor={Producto ? Producto.profundidad_cm : ''}
                    error={Errors && Errors.profundidad_cm}
                    set_dato={setDatoInput}
                    columnas={3}
                    help_text={'Largo del producto en centímetros'}
                  />
                </div>
                <div className={'row'}>
                  <AdminItemField
                    titulo={'Refrigeración'}
                    tipo={'select2'}
                    campo={'refrigeracion'}
                    valor={Producto ? Producto.refrigeracion : 0}
                    error={Errors && Errors.refrigeracion}
                    set_dato={setDatoInput}
                    columnas={3}
                    opciones_select={TIPOS_REFRIGERACION_SELECT}
                    help_text={'Seleccionar tipo de refrigeración requerida'}
                  />
                  <div className={'col-6'}>
                    <label className={'font-weight-bold'}>Etiquetas</label>
                    <ReactTagInput
                      tags={tags[0] !== '' ? tags : []}
                      onChange={(etiquetas) => {
                        setTags(etiquetas)
                      }}
                      placeholder="Escriba y presione la tecla enter"
                    />
                    {Errors && Errors.etiquetas && <div className={'text-danger'}>{Errors.etiquetas}</div>}
                  </div>
                </div>
                {(loginStore.administrador || (Producto && Producto.id === undefined) || Producto === null) && (
                  <div className={'row'}>
                    <AdminItemField
                      titulo={'Costo minorista'}
                      tipo={'numero'}
                      campo={'precio'}
                      valor={Producto ? Producto.precio : ''}
                      error={Errors && Errors.precio}
                      set_dato={setDatoInput}
                      columnas={3}
                      help_text={'Precio de venta a ABi'}
                    />
                    <AdminItemField
                      titulo={'Costo Mayorista'}
                      tipo={'numero'}
                      campo={'precio_mayorista'}
                      valor={Producto ? Producto.precio_mayorista : ''}
                      error={Errors && Errors.precio_mayorista}
                      set_dato={setDatoInput}
                      columnas={3}
                      help_text={'Precio de venta a ABi'}
                    />
                    <AdminItemField
                      titulo={'Cantidad disponible'}
                      tipo={'numero'}
                      campo={'cantidad_disponible'}
                      valor={Producto ? Producto.cantidad_disponible : ''}
                      error={Errors && Errors.cantidad_disponible}
                      set_dato={setDatoInput}
                      columnas={3}
                    />
                    <AdminItemField
                      titulo={'Cantidad Mayorista'}
                      tipo={'numero'}
                      campo={'cantidad_mayorista'}
                      valor={Producto ? Producto.cantidad_mayorista : ''}
                      error={Errors && Errors.cantidad_mayorista}
                      set_dato={setDatoInput}
                      columnas={3}
                    />
                  </div>
                )}
                <div className={'row mb-2'}>
                  {loginStore.administrador && (
                    <div className={'col-3'}>
                      <SelectorModal
                        titulo={'Proveedores'}
                        titulo_label={'Proveedor'}
                        listado_elementos={rootStore.fabricantes}
                        listado_elementos_seleccionados={Producto ? Producto.fabricante : null}
                        datos_filtrados={dataSelectorModal}
                        multiple={false}
                        llave_label_opcion={'nombre'}
                      />
                      {Errors && Errors.fabricante && <div className={'text-danger'}>{Errors.fabricante}</div>}
                    </div>
                  )}
                  <div className={'col-4'}>
                    <SelectorModal
                      titulo={'Categoría'}
                      titulo_label={'Categoría'}
                      listado_elementos={rootStore.categorias}
                      listado_elementos_seleccionados={Producto && Producto.categoria ? Producto.categoria.id : null}
                      datos_filtrados={dataSelectorModalCategoria}
                      multiple={false}
                      llave_label_opcion={'nombre'}
                    />
                    {Errors && Errors.categoria && <div className={'text-danger'}>{Errors.categoria}</div>}
                  </div>
                  <div className={'col-4'}>
                    <label className={'font-weight-bold'}>Video del producto (opcional)</label>
                    <VideoLoaderModal
                      titulo_modal={'Video del producto'}
                      titulo_label={'Seleccionar el video'}
                      on_select={videoSelected}
                      preview={Producto ? Producto.video : null}
                    />
                  </div>
                </div>
                <div className={'row'}>
                  {loginStore.administrador && (
                    <AdminItemField
                      titulo={'Aprobado'}
                      tipo={'boolean'}
                      campo={'activo'}
                      valor={Producto ? Producto.activo : ''}
                      set_dato={setDatoInput}
                      columnas={4}
                    />
                  )}
                </div>
                <div className={'form-group text-right'}>
                  <a onClick={() => history.push('/admin/producto/' + Estado)} className={'btn btn-light'}>
                    Cancelar
                  </a>
                  <button
                    className={'btn btn-primary ml-2'}
                    disabled={Loading}
                    onClick={() => {
                      if (Producto) {
                        if (Producto.activo && !loginStore.administrador) {
                          setAlert(true)
                          setTituloAlert('Atención')
                          setTextoAlert(
                            'Al editar el producto pasará a estar pendiente de aprobación por parte del staff de ABi',
                          )
                        } else {
                          guardar()
                        }
                      } else {
                        guardar()
                      }
                    }}
                  >
                    {Loading && <IonSpinner name="crescent" />}
                    {!Loading && <span>Guardar</span>}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminProductoUpdate
